import { PropsWithChildren, ReactNode } from 'react';

import Icon from 'components/ui/Icon';
import Typography from 'components/ui/Typography';

import classNames from 'classnames';

interface InfoProps extends PropsWithChildren {
  customizedListStyle?: ReactNode;
  iconName?: IconTypes;
  iconSize?: 16 | 20 | 24;
  iconColor?: string;
  typoSize?: 'xs' | 'sm' | 'base' | 'lg';
  shouldCustomizedContent?: boolean;
}

const ICON_SIZE_MAPS = {
  16: 'text-16',
  20: 'text-20',
  24: 'text-24'
};

const Info = ({
  customizedListStyle = false,
  iconSize = 24,
  iconColor = 'text-gray-600',
  iconName = 'icon-check-checkmark-fill',
  typoSize = 'lg',
  children,
  shouldCustomizedContent = false
}: InfoProps) => {
  return (
    <div className="flex items-start gap-x-3">
      {customizedListStyle ? (
        <div className="inline-flex shrink-0">{customizedListStyle}</div>
      ) : (
        <Icon name={iconName} size={ICON_SIZE_MAPS[iconSize]} color={iconColor} />
      )}
      {shouldCustomizedContent ? (
        <>{children}</>
      ) : (
        <Typography size={typoSize} color="text-gray-800">
          {children}
        </Typography>
      )}
    </div>
  );
};

interface InfoListProps extends PropsWithChildren {
  gap?: 4 | 8 | 12 | 16 | 20 | 24;
}

const GAP_MAPS = {
  4: 'gap-y-1',
  8: 'gap-y-2',
  12: 'gap-y-3',
  16: 'gap-y-4',
  20: 'gap-y-5',
  24: 'gap-y-6'
};

const InfoList = ({ gap = 24, children }: InfoListProps) => {
  return <div className={classNames('flex flex-col', GAP_MAPS[gap])}>{children}</div>;
};

Info.List = InfoList;

export default Info;
